.logRegModal {
    position: absolute;
    width: 420px;
    height: 500px;
    border-radius: 10px;

    background: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 14px 0 rgb(220 136 136 / 20%);
    z-index: 999999999;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    animation: all 1s;
    padding-bottom: 1rem;
}
.logRegModal-container {
    position: relative;
    padding-top: 32px;
    background: #fff;
}
.logReg-logo {
    background: url(//ae01.alicdn.com/kf/H499c11b7e0094a2294b33e4af09ebea34.png)
        no-repeat;
    background-size: cover;
    width: 125px;
    height: 31px;
    margin: 0 auto 5px;
}
.logRegModal-container ul {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
    justify-content: center;
    padding: 0;
    margin: 0;
    height: 34px;
    position: relative;
}

.logRegModal-container ul li {
    position: relative;
    width: 100px;
    display: inline-block;
    padding: 10px 0 5px;
    margin: 0 15px;
    font-size: 14px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    white-space: nowrap;
    list-style: none;
    cursor: pointer;
}

.selectedLine1 {
    content: '';
    position: absolute;
    left: 22%;
    bottom: 0;
    width: 0;
    border-bottom: 2px solid #ff4747;
    transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
}
.selectedLine2 {
    content: '';
    position: absolute;
    right: 22%;
    bottom: 0;
    width: 0;
    border-bottom: 2px solid #ff4747;
    transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
}
.logReg-form form {
    padding: 2rem 2rem 0 2rem;
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
    width: 100%;
}
.logReg-form form input {
    display: block;
    width: 100%;
    height: 40px;
    padding: 0 16px 0 10px;
    border: 1px solid #ebebeb;
    border-radius: 4px;
    font-size: 14px;
    color: #333;
    outline: 0;
    -webkit-appearance: none;
    box-sizing: border-box;
}
.logReg-form form input:focus {
    border-color: #333;
}
.logReg-form form button {
    margin-top: 30px;
    width: 100%;
    height: 44px;
    background: #ff4747;
    border: 0;
    border-radius: 3px;
    font-size: 14px;
    line-height: 44px;
    color: #fff;
    text-transform: uppercase;
    padding: 0;
    font-family: Open Sans;
    font-weight: 600;
    outline: 0;
    cursor: pointer;
}
.logReg-form a {
    font-size: 12px;
    color: #2e9cc3;
    text-decoration: none;
    transform: translateY(-5px);
}
.logReg-quick {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    transform: translateY(-14px);
    padding-bottom: 1rem;
}
.google {
    width: 54px;
    height: 54px;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url(https://www.freepnglogos.com/uploads/google-logo-png/google-logo-png-suite-everything-you-need-know-about-google-newest-0.png);
}
.w100 {
    width: 100px;
}
.closeLogReg {
    float: right;
    padding-right: 1rem;
    font-size: 1.1rem;
    color: rgb(139, 131, 131);
    cursor: pointer;
    animation: all 0.2s;
}

.closeLogReg:hover {
    color: #333;
}
.benifits span:first-child {
    display: inline-block;
    width: 20px;
    height: 12px;
    background: url(//ae01.alicdn.com/kf/H6d1b17af1f8a41f9a6ec5048e65084c6k.png)
        no-repeat;
    background-size: contain;
    vertical-align: middle;
}

.benifits span:last-child {
    font-size: 12px;
    color: #ff4747;
    margin-left: 4px;
    line-height: 18px;
    vertical-align: middle;
}

.logReg-quick {
    margin: 8px 0;
    font-size: 12px;
    line-height: 16px;
    text-align: center;
    color: #999;
}
.logReg-quickText {
    margin-bottom: 20px;
    height: 22px;
    text-align: center;
    font-size: 16px;
    color: #999;
    padding-top: 10px;
}
